.page-search {
  display: flex;
  flex-direction: column;
}
.page-search .header {
  padding: 10px 17px;
  border-bottom: 1px solid #e8e8e8;
}
.page-search .header .searchbox {
  position: relative;
  height: 44px;
  line-height: 22px;
  padding: 10px 0 10px 14px;
  border: 1px solid #333;
  border-radius: 2px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
}
.page-search .header .searchbox .input {
  flex: 1 1;
  border: 0;
  outline: 0;
  font-size: 16px;
  color: #222;
  caret-color: red;
  padding-right: 30px;
  background-color: transparent;
}
.page-search .header .searchbox .input:disabled {
  background-color: transparent;
  color: #aaa;
}
.page-search .header .searchbox .searchbtn {
  width: 60px;
  text-align: center;
  border-left: 1px solid #e8e8e8;
  font-size: 16px;
  font-weight: bolder;
}
.page-search .header .searchbox .searchbtn.loading {
  background: url(data:image/gif;base64,) no-repeat center;
  background-size: 24px;
}
.page-search .header .searchbox .clear {
  position: absolute;
  top: 10px;
  right: 63px;
  height: 24px;
  width: 24px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
  display: none;
}
.page-search .header .searchbox .clear.show {
  display: block;
}
.page-search .body {
  flex: 1 1;
  overflow: hidden;
  overflow-y: auto;
}
.page-search .loading-content {
  text-align: center;
  font-size: 14px;
  color: #777;
  padding-top: 30px;
}
.page-search .associatewords {
  padding: 0 17px 10px;
}
.page-search .associatewords .word {
  line-height: 46px;
  color: #222;
  font-weight: bolder;
  padding-left: 30px;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 24px;
  border-bottom: 1px solid #e8e8e8;
}
.page-search .associatewords .word .match {
  color: #999;
  font-weight: normal;
}
.page-search .guessword .guess-tit {
  margin: 0 17px;
  font-size: 14px;
  color: #999;
  line-height: 40px;
}
.page-search .guessword .guess-list {
  display: flex;
  flex-wrap: wrap;
}
.page-search .guessword .guess-list .guess-item {
  position: relative;
  width: 50%;
  padding: 0 17px;
  line-height: 38px;
  color: #222;
}
.page-search .guessword .guess-list .guess-item.rec {
  padding-left: 40px;
  background: url(data:image/png;base64,) no-repeat 17px center;
  background-size: 17px;
}
.page-search .guessword .guess-list .guess-item:nth-child(odd)::after {
  content: " ";
  display: block;
  position: absolute;
  right: 0;
  top: 11px;
  width: 1px;
  height: 16px;
  background-color: #e8e8e8;
}
.page-search .guessword .guess-list .guess-item:last-of-type::after {
  display: none;
}
.page-search .resultword .result-tit {
  margin: 0 17px;
  font-size: 14px;
  color: #999;
  line-height: 40px;
}
.page-search .resultword .noresult {
  margin: 20px 17px 0;
  text-align: center;
  color: #222;
}
.page-search .resultword .noresult b {
  color: red;
}
